@use 'abstracts' as *; 

$gap: 6px;

.breadcrumbs {
    display: none;

    @include from-breakpoint(desktop1) {
        position: relative;
        // transform: translateY(-100%);
        display: flex;
        align-items: center;
        // position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        z-index: 2;
        padding-bottom: 2px;
        padding-left: $gap;
        // outline: 2px dashed gold;
        transition: transform .3s ease-out;
    }

    &__wrapper {
        display: flex;
        flex-direction: column;
        justify-content: center;
        height: 100%;
        padding-top: 6px;
        padding-bottom: 4px;
    }

    &__home {
        display: flex;
        align-items: center;
        gap: $gap;
        font-size: rem(12);

        @include from-breakpoint(tablet2) {
            font-size: clamp-to(1400, 10, 12, 'rem');
        }
    }

    &__ol {
        list-style-type: none;
        display: flex;
        gap: $gap;
        align-items: center;

        & > a,
        & > li > a {
            text-decoration: none;
        }
    }

    &__li {
        display: flex;
        align-items: center;
        color: color('black');
        gap: $gap;
        // outline: 1px dashed lime;

        & > * {
            font-size: rem(12);
            font-weight: 500;

            @include from-breakpoint(tablet2) {
                font-size: clamp-to(1400, 10, 12, 'rem');
            }
        }

        &::after {
            content: "";
            display: inline-block;
            background-position: center;
            background-repeat: no-repeat;
            background-size: contain;
            flex-shrink: 0;
            aspect-ratio: 702/732;
            width: 12px;
            height: auto;
            filter: filter('gray');
            background-image: url('../../dist/svg/chevron-breadcrumbs.svg');
            outline: 1px dashed red;
        }

        &:last-of-type::after {
            display: none;
        }
    }

    &__a {
        color: color('black');
        opacity: 0.7;

        @include from-breakpoint(desktop1) {
            &:hover {
                text-decoration: underline;
            }
        }
    }

    &__span {
        display: inline-block;
        margin-right: 7px;
    }
}